@import url(https://fonts.googleapis.com/css2?family=Noto+Sans:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  font-family: 'Noto Sans', sans-serif;
  color: rgb(62, 247, 62);
  text-align: center;
}

.round-border {
  border-radius: 32px !important;
}

.left-round-border {
  border-radius: 32px !important;
  border-top-right-radius: 0px !important;
  border-bottom-right-radius: 0px !important;
  margin-right: -1px !important;
}

.right-round-border {
  border-radius: 32px !important;
  border-top-left-radius: 0px !important;
  border-bottom-left-radius: 0px !important;
  margin-left: -1px !important;
}

.ant-btn-primary {
  border-color: rgb(13, 219, 13) !important;
  background-color: rgb(62, 247, 62) !important;
}

.home-container {
  margin: 0 auto;
  margin-top: 20%;
  width: 80%;
}

.main-header {
  font-size: 2.5em;
  font-weight: bold;
  margin-bottom: 10px;
}

.back-button {
  margin-top: 10px;
  text-align: left;
}

.content-container {
  margin: 0 auto;
  width: 90%;
}

.header {
  text-align: left;
}

.ant-space {
  width: 100%;
}

.ant-input,
.ant-input-affix-wrapper {
  border-color: #98ff98 !important;
  color: rgb(62, 247, 62) !important;
}

.ant-input-focused,
.ant-input-affix-wrapper-focused {
  box-shadow: 0 0 0 2px rgb(62, 247, 62) !important;
}

.input {
  margin: 0 auto;
  width: 80%;
}

.input-full {
  margin: 0 auto;
  width: 90%;
}

.delete {
  color: red;
}

.button-group {
  width: 90%;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
}

.alert {
  text-align: 'left';
  width: '80%';
  margin: '0 auto';
}

.list-item {
  padding: 6px;
  border-bottom: 1px solid rgba(152, 255, 152, 0.5);
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  text-align: left;
  justify-content: space-between;
  cursor: pointer;
  transition: background-color ease-in-out 1s, color ease-in-out 1s;
}

.list-item:hover {
  background-color: rgba(61, 212, 61, 0.185);
  color: rgb(3, 102, 25);
}

.list-item:last-child {
  border-bottom: 0px;
}

.list-container {
  border: 1px solid rgba(152, 255, 152, 0.9);
  width: 90%;
  margin: 0 auto;
  margin-top: 30px;
}

.header {
  font-size: 1.7em;
  margin-bottom: 30px;
  font-weight: bold;
}

.sub-header {
  font-size: 1rem;
  font-weight: bold;
}

.red {
  color: rgb(160, 30, 73);
}

.loader {
  position: relative;
  margin: 0 auto;
  width: 40%;
}

.loader:before {
  content: '';
  padding-top: 100%;
  display: block;
}

.loader__animated {
  -webkit-clip-path: inset(100% 0 0 0);
          clip-path: inset(100% 0 0 0);
  animation: loader 3s ease-in infinite;
}

.loader img {
  width: 40%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

@keyframes loader {
  0% {
    -webkit-clip-path: inset(100% 0 0 0);
            clip-path: inset(100% 0 0 0);
  }

  50% {
    -webkit-clip-path: inset(0 0 0 0);
            clip-path: inset(0 0 0 0);
  }

  100% {
    -webkit-clip-path: inset(100% 0 0 0 0);
            clip-path: inset(100% 0 0 0 0);
  }
}

